export const Editor = ({socketRef,theme, roomID, onCodeChange}) => {
    
    const editorRef = useRef(null);

    useEffect(() => {
        function init() {
            editorRef.current = Codemirror.fromTextArea(document.getElementById('realTimeEditor'),
            {
                mode: {name: 'javascript', json: true},
                theme: theme,
                autoCloseTags: true,
                autoCloseBrackets: true,
                lineNumbers: true,
            });

            editorRef.current.on('change',(instance, changes)=>{
                console.log('changes', changes);
                const {origin} = changes;
                const code = instance.getValue();
                onCodeChange(code);
                if(origin!=='setValue'){
                    socketRef.current.emit(ACTIONS.CODE_CHANGE,{
                        roomID,
                        code,
                    });
                }
                console.log(code);
            });

            

        }
        init();
    },[theme]);


    useEffect(()=>{

        if(socketRef.current){
            socketRef.current.on(ACTIONS.CODE_CHANGE,({code})=>{
                if(code!== null){
                    editorRef.current.setValue(code);
                }
            });
        }

        return ()=>{
            socketRef.current.off(ACTIONS.CODE_CHANGE);  
        }
    },[socketRef.current]);

    return <textarea id="realTimeEditor"></textarea>;
}

// 
const EditorPage = () => {

    const socketRef = useRef(null);
    const codeRef = useRef(null);
    const location = useLocation();
    const {roomID} = useParams();
    const reactNavigator = useNavigate();
    const [clients,setclients] = useState([]);

    const [theme,setTheme] = useState('dracula');
    const handle=(event)=>{
        // // console.log(event.target.value);
        console.log(theme);
        setTheme(event.target.value);
        console.log(theme);
        // HandleTheme(event);
    }

    // function handle(event){
    //     HandleTheme(event);
    // }
    useEffect(()=>{

        const init=async()=>{
            socketRef.current=await initSocket();
            socketRef.current.on('connect_error',(err)=>handleErrors(err));
            socketRef.current.on('connect_failed',(err)=>handleErrors(err));

            function handleErrors(e) {
                console.log('socket error', e);
                toast.error('Socket connection failed, try again later');
                reactNavigator('/');
            }


            socketRef.current.emit(ACTIONS.JOIN,{
                roomID,
                username: location.state?.username, 
            });


            // listening for joined eventt
            socketRef.current.on(ACTIONS.JOINED, ({clients, username, socketId})=>{
                if(username !== location.state?.username){
                    toast.success(`${username} joined the room`);
                    console.log(`${username} joined`);
                }
                setclients(clients);
                socketRef.current.emit(ACTIONS.SYNC_CODE,{
                    code: codeRef.current,
                    socketId,
                });
            });

            // listening for disconnection
            socketRef.current.on(ACTIONS.DISCONNECTED,({socketId,username})=>{
                toast.success(`${username} has left the room`);
                setclients((prev)=>{
                    return prev.filter(client=>client.socketId !== socketId);
                });
            });

        }
        init();

        return ()=>{
            socketRef.current.off(ACTIONS.JOINED);
            socketRef.current.off(ACTIONS.DISCONNECTED);
            socketRef.current.disconnect();
        }

    },[]);

    async function copyRoomID(){
        try{
            await navigator.clipboard.writeText(roomID);
            toast.success(`Room-id copied to clipboard`);
        }catch(err){
            toast.error(`Error copying to clipboard`);
            console.error(err);
        }
    }

    function leaveRoom(){
        reactNavigator('/');
    }

    if(!location.state){
        return <Navigate to="/"/>;
    }

    return (
        <div className="mainWrap">
            <div className="leftSide">
                <div className="leftInner">
                    <div className="logo">
                        <img className='logo-img' src="/code-sync.png" alt="logo" />
                    </div>
                    <h3>Connected</h3>
                    <div className="clientList">
                        {
                            clients.map((client)=>(
                                <Client key={client.socketId} username={client.username}/>
                            ))
                        }
                    </div>
                </div>
                <button className='btn copy-btn' onClick={copyRoomID}>Copy Room-ID</button>
                <button className='btn leave-btn'onClick={leaveRoom}>Leave</button>
            </div>
            <div className="editorSide">
                <Editor socketRef={socketRef} theme={theme} roomID={roomID} onCodeChange={(code)=>{codeRef.current = code;}}/>
            </div>
            <div className="rightSide">
                <select name="mode" id="mode">
                    <option value="c">C</option>
                    <option value="js">Java Script</option>
                    <option value="p">Python</option>
                </select>
                <select name="theme" id="theme" value={theme} onChange={handle}>
                    <option value="dracula">Dracula</option>
                    <option value="xq-light">XQ LIGHT</option>
                    <option value="qw">qw</option>
                    <option value="as">as</option>
                    <option value="df">df</option>
                </select>
            </div>
        </div>
    );
};

export default EditorPage;